er-component {\r\n    display: none;\r\n  }\r\n}\r\n\r\n@media screen and (max-width: $handphone) {\r\n  .menu-content {\r\n    display: flex;\r\n    justify-content: space-around !important;\r\n    align-content: center !important;\r\n\r\n    h4 {\r\n        margin: 5px 0px;\r\n    }\r\n  }\r\n  .form-review {\r\n    width: 100% !important;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: $handphone) and (max-width: $tablet) {\r\n  .container-detail {\r\n    width: 100%;\r\n  }\r\n  .card {\r\n    padding-bottom: 0px;\r\n    \r\n    .card-description {\r\n      & p {\r\n        overflow: hidden;\r\n        display: -webkit-box;\r\n        -webkit-line-clamp: 4;\r\n        -webkit-box-orient: vertical;\r\n      }\r\n    }\r\n  }\r\n  resto-list {\r\n    .container_restoList {\r\n      grid-template-columns: 1fr 1fr;\r\n    }\r\n  }\r\n}\r\n\r\n@media only screen and (max-width: $tablet) {\r\n  .container-detail {\r\n    width: 100% !important;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: $tablet) and (max-width: 1000px) {\r\n  resto-list {\r\n    .container_restoList {\r\n      grid-template-columns: repeat(3, 1fr);\r\n    }\r\n  }\r\n\r\n  .form-review {\r\n    width: 100%;\r\n  }\r\n}\r\n\r\n\r\n@media screen and (max-width: $desktop) {\r\n  .hamburger {\r\n    display: none;\r\n  }\r\n}","main {\r\n  h2 {\r\n    text-align: center;\r\n    color: $primary-color;\r\n    border: none;\r\n  }\r\n}\r\n\r\n.container_restoList {\r\n  display: grid;\r\n  grid-template-columns: repeat(4, 1fr);\r\n  grid-gap: 20px;\r\n  padding: 30px;\r\n  margin-bottom: 20px;\r\n\r\n  .card {\r\n    border: 1px solid #ccc;\r\n    padding: 10px;\r\n    border-radius: 10px;\r\n    overflow: hidden;\r\n    padding-bottom: 1px;\r\n    position: relative;\r\n    box-shadow: 0 4px 8px 0 #00000033;\r\n\r\n    .card-title {\r\n      font-size: large;\r\n      font-weight: bold;\r\n      margin-top: 2px;\r\n      color: $primary-color;\r\n      text-decoration: none;\r\n      padding: 15px 0p