            cursor: pointer;\n            padding: 13px;\n            font-size: 1rem;\n            font-weight: bold;\n            border: none;\n            border-radius: 10px;\n            color: $primary-color;\n            background-color: white;\n\n            &:hover {\n                background-color: #cfcfcf;\n            }\n        }\n    }\n}","\n.loading {\n  top: 50%;\n  left: 42%;\n  position: fixed;\n  display: inline-flex;\n  border: 10px solid #f3f3f3;\n  border-top: 10px solid $primary-color;\n  border-radius: 50%;\n  width: 100px;\n  height: 100px;\n  box-sizing: border-box;\n  animation: spin 1s linear;\n  z-index: 1000;\n\n  &.hide {\n      display: none;\n      animation: none; \n  }\n}\n  \n  @keyframes spin {\n    to {\n      transform: rotate(360deg);\n    }\n  }\n  "],"sourceRoot":""}]);
